@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500&display=swap);
body, h1, h2, h3, h4, h5, h6 {
    border: 0;
    padding: 0; 
    margin: 0;
}

body, h2 {
    font-family: 'Roboto', sans-serif;
    font-weight: 300;
}
a{
    text-decoration: none;
    color: black;
}
h1 {
    font-weight: 500;
}
h3 {
    font-size: 2rem;
    font-weight: 400;
    margin: 2rem 0;
    background-color: rgba(200,200,200,.1);
    padding: 1rem;
}
h4 {
    font-size: 1.7rem;
    font-weight: 500;
}
h5 {
    font-weight: 400;
    font-size: 1.5rem;
}
h6 {
    font-weight: 400;
    font-size: 1.2rem;
}
p {
    max-width: 800px;
}

.Resume {
    display: flex;
    flex-direction: column;
    padding: 2rem 1rem;
    width: 100vw;
    max-width: 1500px;
    margin: auto;
    box-sizing: border-box;
}
.Resume-content{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between
}
.Resume-main{
    flex: 2 1 520px;
    margin: 0 1rem;
}
.Resume-sidebar{
    flex: 1 1 100px;
    margin: 0 1rem;
}
.Introduction {
    background-color: rgba(200,200,200,.1);
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    padding: 3rem 0;
    margin: 0 1rem;
}
.Introduction-avatar {
    width: 200px;
    height: 200px;
    padding: 0 2rem;
    border-radius: 50%;
    margin: auto;
}
.Introduction-wrapper{
    padding: 1rem 2rem;
    flex: 1 1 350px;
}
.Introduction-name{
    font-size: 3rem;
}
.Introduction-title{
    font-size: 1.2rem;
    margin-top: .5rem;
}           
.Education {
    display: flex;
    flex-direction: column;
    padding-bottom: 3rem;
    padding: 0 1rem;
}

.Education-entity{
    margin: 1rem 0;
}

.Education-entity-place {
    display: flex;
    align-items: center;
}

.Education-entity img {
    max-height: 45px;
    max-width: 80px;
}

.Education-entity-name {
    margin-left: .5rem;
}

.Education-entity-details {
    display: flex;
    flex-direction: column;
    margin-top: .5rem;
}

.Education-entity-duration{
    opacity: .8;
    font-size: .8rem;
    align-self: flex-end;
}
.Skills {
    display: flex;
    flex-direction: column;
    border-bottom: 1px solid rgba(200,200,200, .2);
    padding: 0 1rem;
    padding-bottom: 3rem;
}
.Skills-skill{
    display: flex;
    align-items: center;
    margin: .5rem 0;
}

.Skills-skill img{
    width: 20px;
    margin-right: .5rem;
}
.Languages {
    display: flex;
    flex-direction: column;    
    border-bottom: 1px solid rgba(200,200,200, .2);
    padding: 0 1rem;
    padding-bottom: 3rem;
}
.Languages-language{
    margin: .5rem 0;
}
.Contact {
    display: flex;
    flex-direction: column;    
    padding: 0 1rem;
    padding-bottom: 3rem;
}
.Contact-type{
    display: flex;
    align-items: center;
    margin: .5rem 0;
}

.Contact-type img{
    width: 20px;
    margin-right: .5rem;
}

.Contact-type a{
    text-decoration: none;
    color: black;
}
.Experience {
    display: flex;
    flex-direction: column;    
    padding: 0 1rem;
}
.Experience-work {
    display: flex;
    flex-direction: column;
    padding: 3rem 0;
    border-bottom: 1px solid rgba(200,200,200, .2);
}
.Experience-work:first-child{
    padding-top: 0;
}
.Experience-work-title {
    display: flex;
    align-items: baseline;
}

.Experience-work-duration {
    font-size: 1rem;
    margin: 0 1rem;
    opacity: .5;
    margin-left: 1rem;
}

.Experience-work-place {
    display: flex;
    align-items: center;
    margin: .5rem 0;
}

.Experience-work-place img {
    max-height: 45px;
    max-width: 100px;
    justify-self: center
}

.Experience-work-place div {
    display: flex;
    flex-direction: column;
    margin-left: 1rem;
}

.Experience-work-tool{
    display: flex;
    align-items: center;
    margin: .5rem 1rem;
}

.Experience-work-tool img{
    width: 20px;
    margin-right: .5rem;
}
